// slider-works styles
.m-slider-works{
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  height: auto;
  padding-top: 25px;
  @include media('height<phone') {
    padding-top: 15px;
  }
  &__icon{
    height: inherit;
    width: inherit;
    fill: $c-dark-blue;
    transition: fill $t-normal;
  }
  &__link{
    align-self: flex-end;
    height: 40px;
    width: 50px;
    margin-right: 15px;
    @include media('height<phone') {
      height: 25px;
      width: 30px;
    }
    @include media('<tablet') {
      display: none;
    }
    &:hover{
      .m-slider-works__icon{
        fill: darken($c-orange, $amount: 25) ;

      }
    }
  }
  &__items{
    padding-top: 15px;  
    height: calc(100% - 40px);
    position: relative;
    @include media('height<phone') {
      padding-top: 10px;
      height: calc(100% - 25px);
    }
    @include media('<tablet') {
      height: calc(100% - 70px);
    }
    @include media('<phone') {
      height: calc(100% - 110px);
    }
  }
  &__item{
    opacity:0;
    transition: opacity 1s;
    @include absolutely; 
    z-index: 0;
    &--active{
      opacity:1;
      z-index: 1;
    }
  }
}