// section-technology styles
.o-section-technology{
  position: relative;
  &__icon{
    position: absolute;
    bottom: 0;
    left: 0;
    top: 0;
    // transform: rotate(90deg);
    height: 100%;
    width: 700px;
    stroke:#D3CA5D;
    fill:#D3CA5D;
    opacity: .5;
    @include media('<tablet') {
      display: none;
    }
  }
  &__content{
    background-color: $c-yellow;
    display: flex;
    justify-content: center;
    height: 100%;
  }
  &__container{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 0;
  }
  &__title{
    font-size: $fs-72;
    @include media('<screen') {
      font-size: $fs-55;      
    } 
    @include media('<desktop') {
      font-size: $fs-40;      
    } 
    @include media("height<700px") {
      font-size: $fs-55;
    }
    @include media("height<600px") {
      font-size: $fs-40;
    }
    @include media("height<600px") {
      font-size: $fs-40;
    }
    @include media('<tablet') {
      font-size: $fs-40;    
      padding-top: 5px;  
    } 
    @include media('<phone') {
      font-size: $fs-30;    
      padding-top: 5px;  
    } 
    @include media("height<450px") {
      font-size: $fs-25;
    }
  }
  &__items{
    display: inline-flex;
    flex-direction: column;
    justify-content: space-around;
  }
}