// item-intro styles
.m-item-intro{
  position: relative;
  height: inherit;
  @include backgroundCoverCenter;

  &__content{
    background-color: rgba($c-dark, $alpha: .65);
    height: inherit;
    display: inline-flex;
    align-items: center;
    width: 100%;


  }
  &__container{
    height: inherit;
    display: inline-flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    color: darken($c-light, $amount: 4);
    font-family: $f-montserrat;
  }
  &__title{
    font-size: $fs-48;
    font-weight: $fw-bold;
    text-transform: uppercase;
    @include media('<tablet') {
      font-size: $fs-34;
    }
    @include media('height<380px') {
    font-size: $fs-34;
    }
  }
  &__subtitle{
    font-size: $fs-24;
    max-width: 590px;
    padding: 50px 0;
    text-align: end;
    @include media('height<380px') {
      padding: 25px 0;
    }
  }
  &__button{
    width: 225px;
    &-text{
      height: 50px;
      width: 100%;
      font-size: $fs-14;
      // font-weight: $;
      & span{
        background: #cab002;
        background: linear-gradient(to right,  #cab002 0%,#fbeea3 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff8282', endColorstr='#e178ed',GradientType=1 );
          background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
  }
}