// item-work styles
.m-item-work{
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  color:$c-dark-blue;
  font-family: $f-roboto;
  width: 100%;
  height: 100%;
  
  &__title{
    font-size: $fs-48;
    @include media('<screen') {
      font-size: $fs-30;      
    }
    @include media("height<tablet") {
      font-size: $fs-30;
    }
    @include media('height<phone') {
      font-size: $fs-22;
    }
    @include media("<phone") {
      font-size: $fs-22;
    }
  }
  &__date{
    font-size: $fs-30;
    font-weight: $fw-light;
    @include media('<screen') {
      font-size: $fs-25;      
    }
    @include media("height<tablet") {
      font-size: $fs-25;
    }
    @include media('height<phone') {
      font-size: $fs-18;
    }
    @include media("<phone") {
      font-size: $fs-18;
    }
  }
  &__link{
    display: inline-flex;
    margin: 15px 0 15px 40px;
    width: 225px;
    @include media("height<tablet") {
      width: 150px;
      margin-left: 20px;
    }
    @include media('height<phone') {
      width: 115px;
      margin-left: 20px;
    }
    @include media("<phone") {
      margin-left: 0;
    }
  }
  &__button-text{
    width: 100%;
    height: 50px;
    font-size: $fs-18;
    @include media("height<tablet") {
      height: 40px;
    }
    @include media('height<phone') {
      height: 30px;
      font-size: $fs-16;
    }

    // font-weight: $;
    & span{
      background: #f63fe7;
      background: linear-gradient(to right,  #ff7c00 0%,#f63fe7 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff8282', endColorstr='#e178ed',GradientType=1 );
        background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    &:hover {
      & .a-button-text__svg-rect{
        @include media("height<tablet") {
          stroke-dasharray: 100, 323;
          stroke-dashoffset: 257;
        }
      }
    }
  }
  &__img{
    height: 600px;
    width: 100%;
    align-self: flex-end;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: bottom right;
    @include media("height<700px") {
      height: 350px;
    }
    @include media('<tablet') {
      height: 100%;
      
      margin-top: 15px;
    }
  }
}