// button-text styles
.a-button-text{
  position: relative;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  
  &__svg{
    @include absolutelyFull;
  }
  &__svg-rect{
    stroke-width: 3;
    stroke-dasharray: 353, 0;
    stroke-dashoffset: 0;
    -webkit-transition: all 600ms ease;
    transition: all 600ms ease;
  }
  &:hover {
    & .a-button-text__svg-rect{
      stroke-dasharray: 175, 523;
      stroke-dashoffset: 457;
    }
  }
}
