// contact styles
.m-contact{
  font-size: $fs-20;
  @include media('height<phone') {
    font-size: $fs-16;
  }
  &__title{
    padding-bottom: 3vh;
    letter-spacing: 1px;  
    @include media('height<phone') {
      padding-bottom: 2vh;
    }
  }
  &__items{
    display: inline-flex;
    flex-direction: column;
    padding-bottom: 5vh;
    letter-spacing: 2px;  
    line-height: 1.8;
    @include media('height<phone') {
      line-height: 1.2;
      letter-spacing: 1px;  
    }
  }
  &__item{
    color: lighten($c-dark, $amount: 8);
    transition: color $t-normal;
    &:hover{
      color: lighten($c-dark, $amount: 30);
    }
  }
}