// section-about styles
.o-section-about{
  position: relative;
  &__icon{
    position: absolute;
    bottom: 0;
    left: -5vw;
    height: 650px;
    width: 600px;
    stroke: darken($c-light-blue, $amount: 4);
    fill: darken($c-light-blue, $amount: 4);
    @include media('<tablet') {
      display: none;
    }
  }
  &__content{
    background-color: $c-light-blue;
    display: flex;
    justify-content: center;
    height: 100%;
  }
  &__container{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 0;
  }
  &__items{
    display: inline-flex;
    flex-direction: column;
    justify-content: space-around;
  }
}