// section-contact styles
.o-section-contact{
  position: relative;
  &__icon{
    position: absolute;
    bottom: 0;
    left: -5vw;
    height: 650px;
    width: 600px;
    stroke: darken($c-light-green, $amount: 8);
    fill: darken($c-light-green, $amount: 8);
    @include media('<tablet') {
      display: none;
    }
  }
  &__content{
    background-color: $c-light-green;
    display: flex;
    justify-content: center;
    height: 100%;
  }
  &__container{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 0;
    @include media('<tablet') {
      // align-items: flex-start;
    }
  }
  &__items{
    display: inline-flex;
    flex-direction: column;
    font-family: $f-roboto;
    color: lighten($c-dark, $amount: 8);
  }
  &__subtitle{
    display: inline-flex;
    align-items: center;
    font-family: $f-montserrat;
    font-size: $fs-40;
    letter-spacing: 2px;
    padding-bottom: 5vh;
    @include media('height<phone') {
      padding-bottom:0vh;
      font-size: $fs-24;
    }
    @include media('<tablet') {
      padding-bottom:2vh;
      font-size: $fs-24;
    }
    &--light{
      color: darken($c-light, $amount: 4);
    }
  }
  &__link{
    margin-left: 20px;
    padding: 13px;
    background-color: lighten($c-dark, $amount: 20);
    display: inline-flex;
    transition: background-color $t-normal;
    @include media('height<phone') {
      padding: 10px;
    }
    &-icon{
      height: 24px;
      width: 24px;
      fill:$c-light;
      @include media('height<phone') {
        width: 15px;
        height: 15px;
      }
    }
    &:hover{
      background-color: lighten($c-dark, $amount: 27);
      
    }
  }
}