// item-description-image styles
.m-item-description-image{
  display: inline-flex;
  flex-wrap: wrap;
  padding: 5vh 15px;
  align-items: center;
  &__img{
    // max-width: 145px;
    height: 13vh;
    padding: 0;
  }
  &__text{
    padding-left: 30px;
    font-family: $f-roboto;
    font-size: $fs-32;
    color: $c-dark-blue;
    padding-right: 0;
    @include media('<screen') {
      font-size: $fs-28;      
    }
    @include media('<desktop') {
      font-size: $fs-22;      
    }
    @include media("height<700px") {
      font-size: $fs-28;
    }
    @include media("height<600px") {
      font-size: $fs-22;
    }
    @include media("height<450px") {
      font-size: $fs-18;
    }
    @include media('<phone') {
      padding-top: 5px;  
      letter-spacing: 0px;
      font-size: $fs-18;
    } 
  }
  &--column{
    justify-content: center;
    padding: 2vh 15px;

    & .m-item-description-image{
      &__img{
        height: 7vh;
        padding: 0 15px;
      }
      &__text{
        padding:3vh 15px 0;
        font-size: $fs-20;
        font-weight: $fw-extrabold;
        letter-spacing: 2px;
        color: lighten($c-dark, $amount: 2);
        text-align: center;
        @include media('<screen') {
          font-size: $fs-18;      
        }
        @include media('<desktop') {
          font-size: $fs-16;      
        }
        @include media("height<700px") {
          font-size: $fs-18;
          padding-top: 2vh;
        }
        @include media("height<600px") {
          font-size: $fs-16;
        }
        @include media("height<450px") {
          font-size: $fs-14;
          letter-spacing: 1px;
          font-weight: $fw-bold;
        }
        @include media('<phone') {
          padding-top: 5px;  
          letter-spacing: 0px;
          font-size: $fs-13;
        } 
        @include media("height<415px") {
          font-size: $fs-12;
        }
      }
    }
  }
}