// section-works styles
.o-section-works{
  position: relative;
  &__icon{
    position: absolute;
    bottom: 0;
    left: -5vw;
    height: 650px;
    width: 600px;
    stroke: darken($c-orange, $amount: 8);
    fill: darken($c-orange, $amount: 8);
    @include media('<tablet') {
      display: none;
    }
  }
  &__content{
    background-color: $c-orange;
    display: flex;
    justify-content: center;
    height: 100%;
    position: relative;
  }
  &__container{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 0;
    position: static;
  }
  &__title{
    // padding-left: 4vh;
  }
  &__slider{
    top: 0;
    right: 0;
    position: absolute;
    // padding: 25px 0 0;
    height: 100%;
    @include media('<tablet') {
      position: relative;
    }
  }
  
}