html {
  -webkit-text-size-adjust: 100%; /* Prevent font scaling in landscape while allowing user zoom */
  font-size: 14px;
}

* {
  box-sizing: border-box;
}

body{
  background-color: $c-dark;
  overflow: hidden;
  opacity: 0;
}

.main{
  transition: all $t-normal;
}
.fp-slidesContainer{
  display: inline-flex;
}
section{
  overflow: hidden;
}