// modal-window styles
.main--blur{
  filter: blur(5px);
}
.m-modal-window {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($c-dark, $alpha: .80);
  opacity: 0;
  visibility: hidden;
  transform: scale(1.1);
  transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
  z-index: 3;
  &--show {
    opacity: 1;
    visibility: visible;
    transform: scale(1.0);
    transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
  }
  &__content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba($color: lightgray, $alpha: .80) ;
    max-width: 570px;
    max-height: 310px;
    border-radius: 40px;
    padding: 30px 15px;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    font-family: $f-roboto;
    font-size: $fs-22;
    color:lighten($c-dark, $amount: 42) ;
    @include media('<maxPhone') {
      width: 95%;
    }
  }
  &__icon {
    position: absolute;
    top: 15px;
    right: 25px;
    width: 15px;
    cursor: pointer;
    fill: $c-dark-blue;
    transition: fill $t-normal;

    &:hover {
      fill: lighten($c-dark-blue, $amount: 10) ;
    }
  }
  &__title{
    font-size: $fs-37;
    letter-spacing: 2.4px;
    font-weight: $fw-bold;
    text-transform: uppercase;
    color: $c-dark-blue;
  }
  &__label{
    letter-spacing: 1.2px;
    text-align: center;
    &.error{
      color:brown;
    }
  }
  &__input{
    border:1px solid lighten($c-dark, $amount: 42) ;
    border-radius: 20px;
    color:lighten($c-dark, $amount: 42) ;
    background-color: transparent;
    font-size: $fs-20;
    font-family: $f-roboto;
    outline: none;
    padding: 10px 15px;
    max-width: 410px;
    width: 100%;
    &.error{
      border-color:brown;
    }
  }
  &__button{
    color: $c-dark-blue;
    border: 2px solid $c-dark-blue;
    text-transform: uppercase;
    border-radius: 20px;
    background-color: transparent;
    font-family: $f-roboto;
    padding: 5px 15px;
    font-size: $fs-20;
    letter-spacing: 1.5px;
    font-weight: $fw-bold;
    align-self: flex-start;
    margin-left: 66px;
    transition:  background-color .5s;
    cursor: pointer;
    outline: none;
    @include media('<maxPhone') {
      margin-left: 0px;
    }
    &:hover{
      background-color: rgba($color: lightgray, $alpha: .5) ;
    }
  }
  &__thanks{
    font-size: $fs-35;
    color: $c-dark-blue;
    text-align: center;
    margin: auto;
    display: none;
    @include media('<phone') {
      font-size: $fs-22;  
    }
  }
}
