// title styles
.a-title{
  font-size: $fs-100;
  font-weight: $fw-extrabold;
  font-family: $f-nunito;
  position: relative;
  color: $c-dark-blue;
  text-transform: uppercase;
  line-height: 1.2em;
  display: inline-flex;
  flex-direction: column;
  margin: 0;
  @include media('<screen') {
    font-size: $fs-75;      
  } 
  @include media('<desktop') {
    font-size: $fs-55;      
  }
  @include media("height<700px") {
    font-size: $fs-75;
  }
  @include media("height<600px") {
    font-size: $fs-55;
  }
  @include media('<tablet') {
    font-size: $fs-45;
  }
 
  @include media('<500px') {
    font-size: $fs-40;
  }
 
  &__line{
    margin-top: 20px;
    height: 9px;
    border-radius: 4.5px;
    width: 50%;
    background-color: $c-gold;
    @include media('<tablet') {
      margin-top: 10px;
    }
  }
}