// section-we-do styles
.o-section-we-do{
  position: relative;
  &__icon{
    position: absolute;
    bottom: -50px;
    left: 0;
    // transform: rotate(90deg);
    height: 650px;
    width: 700px;
    stroke: darken($c-pink, $amount: 4);
    fill: darken($c-pink, $amount: 4);
    @include media('<tablet') {
      display: none;
    }
  }
  &__content{
    background-color: $c-pink;
    display: flex;
    justify-content: center;
    height: 100%;
  }
  &__container{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 0;
  }
  &__items{
    display: inline-flex;
    flex-direction: column;
    justify-content: space-around;
  }
}