// button-icon styles
.a-button-icon{
  bottom:10px;
  right:40px;
  position:fixed;
  z-index:2;
  height: 150px;
  width: 150px;
  display: none;
  @include media('<phone') {
    right:5px;
    bottom:5px;
  }
  &--active{
    display: block;
  }
  &--transform{
    position:absolute;
    transform-origin: center;
    transition: all 0.5s;
    border-radius: 100%;
    box-sizing:border-box;
    @include absolutelyCenter;
  }
  &__circle{
    border: 2px solid $c-light;
    width:100%;
    height: 100%;
    opacity: .5;
    animation: circle-anim 2.3s infinite ease-in-out;
    &-fill{
      background-color:$c-light;
      width:105px;
      height:105px;
      border: 2px solid transparent;
      animation: circle-fill-anim 2.3s infinite ease-in-out;
    }
    &-small{
      width:77px;
      height:77px;
      cursor: pointer;
      animation:none;
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: .8;    
      transition: opacity .35s;
      &:hover{
        opacity: 1;
      }
    }
  }
  &__icon{
    background: $c-light;
    fill: lighten($c-dark, $amount: 18);
    width:40px;
    height:30px;
    animation: tossing 1.5s infinite;
  }
}

@keyframes tossing {
  0% {
    transform: rotate(-8deg);
  }
  50% {
    transform: rotate(8deg);
  }
  100% {
    transform: rotate(-8deg);
  }
}

@keyframes circle-anim {
  0% {
    transform: rotate(0deg) scale(0.5) skew(1deg);
    opacity: .1;
  }
  30% {
    transform: rotate(0deg) scale(0.7) skew(1deg);
    opacity: .5;
  }
  100% {
    transform: rotate(0deg) scale(1) skew(1deg);
    opacity: .0;
  }
}
@keyframes circle-fill-anim {
  0% {
    transform: rotate(0deg) scale(0.7) skew(1deg);
    opacity: .2;
  }
  50% {
    transform: rotate(0deg) scale(1) skew(1deg);
    opacity: .2;
  }
  100% {
    transform: rotate(0deg) scale(0.7) skew(1deg);
    opacity: .2;
  }
}
